"use client";

import React, { useState, useEffect } from "react";

export default function WebRTCLabApp() {
    const [logs, setLogs] = useState<string[]>([
        "[SYSTEM] WebRTC Lab initialized.",
        "[SYSTEM] Awaiting peer connection...",
    ]);

    useEffect(() => {
        const sequences = [
            "[PEER] Offer generated: a=rtcpmux...",
            "[PEER] Gathered ICE candidates (stun.l.google.com:19302).",
            "[SIGNAL] Sending SDP offer via signaling server...",
            "[SIGNAL] Received answer from remote peer.",
            "[ICE] Connection state change: checking...",
            "[ICE] Connection state change: connected!",
            "[DATA] DataChannel 'chat' opened successfully.",
            "[STREAM] Receiving remote video track (VP8).",
            "..."
        ];

        let i = 0;
        const interval = setInterval(() => {
            if (i < sequences.length) {
                setLogs((prev) => [...prev, sequences[i]]);
                i++;
            } else {
                clearInterval(interval);
            }
        }, 1500);

        return () => clearInterval(interval);
    }, []);

    return (
        <div className="w-full h-full p-4 bg-[#0a0a0a] text-[#00ff00] font-mono text-xs overflow-y-auto flex flex-col">
            <div className="mb-4">
                <div className="text-white bg-blue-900 px-2 font-bold mb-2 w-max border border-blue-500 shadow-[0_0_10px_rgba(0,100,255,0.5)]">
                    P2P_TERMINAL // WEBRTC_LAB
                </div>
            </div>

            <div className="flex-1 flex flex-col gap-1">
                {logs.map((log, idx) => (
                    <div key={idx} className={`${log?.startsWith("[SYSTEM]") ? "text-blue-400" :
                        log?.startsWith("[ICE]") || log?.startsWith("[STREAM]") ? "text-yellow-400" :
                            log?.startsWith("[DATA]") ? "text-green-400" :
                                "text-zinc-400"
                        }`}>
                        <span className="opacity-50 select-none mr-2">
                            {new Date().toISOString().split("T")[1].slice(0, 12)}
                        </span>
                        {log}
                    </div>
                ))}
                {logs.length < 11 && (
                    <div className="animate-pulse text-zinc-600 mt-2">_</div>
                )}
            </div>

            <div className="mt-4 border-t border-zinc-800 pt-2 flex items-center gap-2">
                <span className="text-blue-500 font-bold">{">"}</span>
                <input
                    type="text"
                    className="bg-transparent outline-none flex-1 text-zinc-300 placeholder:text-zinc-600"
                    placeholder="Enter command (e.g. disconnect, send data)..."
                    disabled
                />
            </div>
        </div>
    );
}
