"use client";

import React from "react";
import { useOS } from "@/context/OSContext";
import Window from "./Window";

import RandumaniaApp from "./apps/RandumaniaApp";
import StrangeletThingsApp from "./apps/StrangeletThingsApp";
import WebRTCLabApp from "./apps/WebRTCLabApp";
import KofiApp from "./apps/KofiApp";

export default function OSManager() {
    const { windows } = useOS();

    return (
        <>
            {windows.map((w) => {
                let content = null;
                let width = 600;
                let height = 400;

                switch (w.id) {
                    case "randumania":
                        content = <RandumaniaApp />;
                        width = 720;
                        height = 480;
                        break;
                    case "strangelet":
                        content = <StrangeletThingsApp />;
                        width = 640;
                        height = 400;
                        break;
                    case "webrtc":
                        content = <WebRTCLabApp />;
                        width = 500;
                        height = 350;
                        break;
                    case "kofi":
                        content = <KofiApp />;
                        width = 380;
                        height = 420;
                        break;
                    default:
                        content = <div className="p-4">App not found.</div>;
                }

                return (
                    <Window key={w.id} id={w.id} title={w.title} defaultWidth={width} defaultHeight={height}>
                        {content}
                    </Window>
                );
            })}
        </>
    );
}
